.topbar{
    width: 100%;
    height: 50px;
    background-color: #06283D;
    color: white;
}
.topbarWrapper{
    height: 100%;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.topRight{
    display: flex;
    align-items: center;
}
.topRight svg{
    margin-left: 5px;
}
.topLeft{
    color: rgb(228, 228, 228);
}


